
.height-transition {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  overflow: hidden;
}
.height-enter,
.height-leave {
  height: 0 !important;
}
.chapters {
  width: 100%;
  border-spacing: 0;
  overflow: hidden;
}
.chapters p,
.chapters h3 {
  margin: 0;
}
.chapters tr {
  height: 32px;
  height: 2rem;
}
.chapters .chapter__preview-tr {
  height: auto;
  height: initial;
}
.chapters .chapter__preview-tr td {
  padding: 0;
  border: none;
}
.chapters .chapter__preview {
  height: 180px;
}
.chapters .chapter__preview-inner {
  padding: 16px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: calc(100vw - 32px - 16px);
  overflow-x: auto;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.chapters tbody tr:not(.no-highlight):hover {
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  background-color: #d6d6d6;
}
.chapters td {
  border-top: 1px rgba(0, 0, 0, .12) solid;
}
.chapters th,
.chapters td {
  text-align: right;
  vertical-align: middle;
}
.chapters tr th:nth-child(n+3),
.chapters tr td:nth-child(n+3) {
  padding: 0 32px 0 0;
  padding: 0 2rem 0 0;
}
.chapters tr td:nth-child(2),
.chapters tr th:nth-child(2) {
  text-align: left;
  padding: 0 16px;
  padding: 0 1rem;
}
.chapters__row__index {
  width: 16px;
  width: 1rem;
}
.chapter__row__title {
  min-width: 240px;
  min-width: 15rem;
}
.chapter__row__title a {
  color: #00adb5;
}
.chapter__preview__page-image {
  border: 2px solid #000;
  margin: 2px;
}
.chapter__preview__page-image img {
/* TODO Use a placeholder image instead directly into lazy options */
  min-height: 140px;
  max-height: 140px;
  min-width: 87px;
}
.chapter__preview__page-image img:hover {
  cursor: zoom-in;
}
.spinner-container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
@media (max-width: 700px) {
.spinner-container {
    max-width: 100%;
}
}
@media (max-width: 400px) {
.spinner-container {
    max-width: 400px;
}
}


